import SearchHeader from '@/components/layouts/SearchHeader.vue';
export default {
	components: {
		SearchHeader
	},
	data() {
		return {
			category: [],
			currentTab: 0,
			hasMore: 0,
			list: []
		}
	},
	onLoad() {
		this.getCategory()
		this.getList()
	},
	methods: {
		getList() {
			uni.request({
				url: getApp().globalData.api + 'store/index',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					category_id: this.currentTab
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.list = res.data.data.list
					this.hasMore = res.data.data.has_more
				}
			});
		},
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {

			let cur = e.currentTarget.dataset.current; //cur ： 一级分类编号

			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur
				this.getList(cur)
			}
		},
		getCategory() {
			uni.request({
				url: getApp().globalData.api + 'category/index',
				method: 'GET',
				data: {
					parent: 2,
					open_id: uni.getStorageSync('open_id')
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.category = res.data.data
				}
			});
		}
	}
}
